*
{
margin: 0;
padding: 0;
}
.App-input
{
    text-align: center;
    background-color: lightblue;
    height: 300px;
    color: navy;
    font-size: 18px;
}
input{
    border: 1px solid gray;
    outline: none;
    padding: 10px 5px;
    width: 200px;
}
.App-input button{
    background-color: lightskyblue;
    color: white;
    padding: 10px 20px;
    border-radius: 30px;
    margin-top: 10px;
}
.navActive{
    color: limegreen;

}
nav
{
    background-color: rgb(153, 47, 153);
    padding: 10px 0;
    display: flex;
    justify-content: center;
    margin-top: 5px;
}
nav ul{
    display: flex;
    list-style-type: none;
    justify-content: space-between;
    align-items: center;
    width: 40%;
    color: white;
    font-weight: bold;
}
nav ul li{
    cursor: pointer;
    font-size: 14px;
    transition: all 0.5s ease-in-out;
}
nav ul li:hover
{
    transform: scale(1.4);
}
.App-container
{
    background-color: blue;
    color: white;
    padding: 10px;
}